@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}

@mixin buttons {
  display: block;
  outline: none;
  background-color: $button-default-color;
  border: 1px solid $button-default-color;
  border-radius: 8px;
  width: 100%;
  height: 56px;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 50px;
  color: #fff;
  text-decoration: none;
  box-sizing: border-box;

  &:hover {
    background-color: $main-text-color;
    border-color: $main-text-color;
  }

  &:active {
    background-color: $button-active-color;
    border-color: $button-active-color;
  }
}
